.loader {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 5px dotted teal;
    animation: rotate 15s linear infinite;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(3);
    }
}